.radio {
    cursor: pointer;
    position: relative;
    user-select: none;

    &.rounded {
        .label:before {
            border-radius: 100%;
        }
    }

    .label {
        display: inline-block;
        padding-left: $checkbox-gutter-size;
        color: $checkbox-label-color;
        vertical-align: text-top;
        font-size: 14px;
        line-height: 15px;

        &:before,
        &:after {
            transition: all 0.2s ease-in-out;
        }

        &:before {
            content: "";
            display: block;
            width: $checkbox-box-size;
            height: $checkbox-box-size;
            border: $checkbox-box-border-size solid $checkbox-border-default;
            position: absolute;
            top: -2px;
            left: 0;
            transform: rotate(0deg) scale(1);
        }

        &:before {
            border-radius: $checkbox-checkmark-border-size;
        }

        &:hover::before {
            border-color: $checkbox-border-active;
        }

        &:after {
            content: "";
            display: block;
            width: 8px;
            height: 18px;
            border-width: $checkbox-checkmark-border-size;
            border-style: solid;
            border-color: $checkbox-border-active;
            position: absolute;
            top: -2px;
            left: 0;
            transform: rotate(0deg) scale(0);
        }
    }

    input {
        visibility: hidden;
    }

    input[type="checkbox"],
    input[type="radio"] {
        clip-path: polygon(0 0);
    }

    /* circles, alignment */
    .label::before,
    .label::after {
        border-radius: 100%;
        top: -2px;
    }

    /* radio bullet circle */
    .label::after {
        content: '';
        display: block;
        position: absolute;
        width: 12px;
        height: 12px;
        top: 2px;
        left: 4px;
    }

    &>input:checked+.label::after {
        background-color: transparent;
        height: 12px;
        top: 2px;
        transform: rotate(0deg) scale(1);
    }

    &>input:checked+.label::before {
        background-color: white;
    }
}