@each $color_name, $color_value in $theme-colors {
  .#{$prefix}background-#{$color_name} {
    background-color: $color_value !important;
  }
  .#{$prefix}text-#{$color_name} {
    color: $color_value !important;
  }
  .#{$prefix}border-#{$color_name} {
    border: 1px solid $color_value !important;
  }
  .#{$prefix}border-#{$color_name}--dashed {
    border: 1px dashed $color_value !important;
  }
}

.text-white {
  color: $white;
}