
.text-divider {
    position: relative;
    display: flex;
    align-items: center;

    &::after {
        content: '';
        position: relative;
        height: 1px;
        flex: 1;
        margin-left: 1rem;
        background-color: $border;
    }

    &.center {
        &::before {
            content: '';
            position: relative;
            height: 1px;
            flex: 1;
            margin-right: 1rem;
            background-color: $border;
        }
    }
    
    &.right {
        &::after {
            display: none;
        }

        &::before {
            content: '';
            position: relative;
            height: 1px;
            flex: 1;
            margin-right: 1rem;
            background-color: $border;
        }
    }
}

.section-title {
    @extend .text-divider;
    @extend .h4;
}