html,
body {
  margin: 0 !important;
  // font-weight: $font-weight-default !important;
  font-size: $font-size-base !important;
  font-family: $font-family-primary;
  height: 100%;
  background: $bg-color;
  @include no-scrollbars();
}

//Default styles
html {
  height: 100%;
}

body {
  margin: 0;
  height: 100%;
  background-color: $bg-color;
  color: $text-color;
  font-size: 100%;
  overflow-x: hidden;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

ul {
  &:not(.browser-default) {
    padding-left: 0;
    list-style-type: none;

    &>li {
      list-style-type: none;
      margin: 5px 0;
    }
  }
}

hr {
  margin: 15px 0 20px 0;
}

.network-status-icon {
  @extend .icon;
  position: fixed;
  top: 5px;
  right: 5px;
  z-index: 200;
}

.network-status-strip {
  position: fixed;
  top: 0;
  width: 100%;
  height: 3px;
  z-index: 200;
}