nav.sidenav {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  position: fixed;
  top: 0;
  bottom: 0;
  height: 100%;
  left: calc(-100% - 56px);
  width: calc(100% - 56px);
  background-color: #f9f9f9;
  transition: all 0.3s;
  transition-timing-function: ease-in;
  opacity: 0.9;
  z-index: 8020;

  .sidenav-header {
    padding: 1rem;

    img {
      height: 100%;
    }
  }

  .sidenav-navigation {
    list-style: none;
    margin: 0;

    li {
      display: block;
      position: relative;
      margin: .5rem 0;

      a {
        width: 100%;
        font-size: 1rem;
        color: #5c5c5c;
        padding: 0 2rem;
        text-decoration: none;
        min-height: 48px;
        line-height: 48px;
        display: inline-flex;
        align-items: center;
        color: $text-color;

        &:hover {
          background-color: rgba(0, 0, 0, 0.1);
        }
      }
    }
  }

  .sidenav-footer {
    border-top: 1px solid $border;
    position: absolute;
    bottom: 0;
    vertical-align: baseline;
    color: white;
    width: 100%;
    padding: .5rem 1rem;
    box-sizing: border-box;
  }

  @include sm() {
    left: -250px;
    width: 250px;
  }

  &.is-open {
    left: 0px;
    box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.275);
    opacity: 1;
    transition-timing-function: ease-in;
  }

  &.right {
    right: calc(-100% - 56px);
    left: initial;

    @include sm() {
      right: -250px;
      width: 250px;
    }

    &.is-open {
      left: initial;
      right: 0px;
      box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.275);
      opacity: 1;
      transition-timing-function: ease-in;
    }
  }

  &.dark {
    background-color: $onyx;

    .sidenav-navigation li a {
      color: white;
    }
  }
}