#slideshow {
    position: relative;

    div {
        position: absolute;
    }
}
.tabs {
    display: flex;

    .tab {
        flex: 1;
        cursor: pointer;
        background-color: $tab-inactive-bg;
        padding: $tab-padding;
        color: $white;
        user-select: none;

        &:not(:last-child) {
            border-right: 1px solid $white;
        }

        &:hover {
            background-color: lighten($color: $primary, $amount: 15)
        }

        &.is-active {
            background-color: $tab-active-bg;
            cursor: default;
        }

        &.is-disabled {
            background-color: $tab-disabled-bg;
            color: $tab-disabled-color;
            pointer-events: none;
        }
    }

    &.flat {
        .tab {
            background-color: transparent;
            border: none;
            border-bottom: 2px solid $tab-inactive-bg;
            padding: $tab-padding;
            color: $tab-inactive-bg;

            &:hover {
                border-color: lighten($color: $primary, $amount: 15)
            }
    
            &.is-active {
                border-color: $tab-active-bg;
                color: $tab-active-bg;
                cursor: default;
            }
    
            &.is-disabled {
                border-color: $tab-disabled-bg;
                color: $tab-disabled-color;
                pointer-events: none;
            }
        }
    }
}

.tab-panels {
    background-color: $white;
    padding: $tab-panel-padding;

    .panel {
        display: none;

        &.is-active {
            display: block;
        }
    }
}

