@import 'settings/variables';
@import 'settings/mixins';

main {
    height: 100%;
}

section[name] {
    margin: 3rem 0;
}

.docs-container {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 100%;
    grid-template-areas: "content";
    height: 100%;
}

@media (min-width: 769px) {
    .docs-container {
        grid-template-columns: 250px 1fr;
        grid-template-areas: "navigation content";
        height: 100%;
    }
}

.docs-navigation {
    grid-area: navigation;
    background-color: $secondary;
    padding: 1rem .5rem;
    display: none;
}

@media (min-width: 769px) {
    .docs-navigation {
        display: block;
    }
}

.docs-content {
    grid-area: content;
    padding: 1rem;
    overflow-y: scroll;
}

// Colors

.color-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 20px;
    margin: 2.5rem 0;

    div {
        height: 100px;
    }
}

.color-primary {
    position: relative;
    background-color: $primary;

    &::before {
            content: attr(class);
            position: absolute;
            bottom: 10px;
            left: 10px;
            color: white;
    }
};
