.notification-panel {
    position: absolute;
    top: -100%;
    left: 0;
    right: 0;
    transition: all .4s ease-out;
    opacity: 0;
    will-change: top, opacity;

    &.is-open {
        top: 0;
        opacity: 1;
    }
}

.notification {
    background-color: #fff;
    @include box-shadow(1);
    padding: 1rem;
    display: flex;
    align-items: center;
    position: relative;

    .notification-icon::before {
        content: 'info';
        @extend .icon;
        @extend .icon-36;
        margin-right: 1rem;
    }

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 4px;
        height: 100%;
    }

    .notification-content {
        flex: 1;
    }

    .notification-title {
        font-weight: 700;
        margin-top: 0;
        margin-bottom: 8px;
    }

    .notification-message {
        font-size: 14px;
        margin-top: 0;
        margin-bottom: 0;
        @include line-clamp(1)
    }

    .notification-close {
        @extend %button-base;

        padding: 0;
        color: currentColor;
        margin: 0;
        margin-left: 1rem;

        &::before {
            content: 'close';
            @extend .icon;
        }
    }

    &.success {
        &::before {
            background-color: $success;
        }

        .notification-icon::before {
            content: 'check';
            color: $success;
        }
    }

    &.error {
        &::before {
            background-color: $danger;
        }

        .notification-icon::before {
            content: 'error';
            color: $danger;
        }
    }

    &.info {
        &::before {
            background-color: $info;
        }

        .notification-icon::before {
            content: 'info';
            color: $info;
        }
    }

    &.dark {
        background-color: $onyx;

        .notification-title,
        .notification-message,
        .notification-close {
            color: white;
        }
    }
}