.checkbox {
  cursor: pointer;
  position: relative;
  user-select: none;

  &.rounded {
    .label:before {
      border-radius: 100%;
    }
  }

  .label {
    display: inline-block;
    padding-left: $checkbox-gutter-size;
    color: $checkbox-label-color;
    vertical-align: text-top;
    font-size: 14px;

    &:before,
    &:after {
      transition: all 0.2s ease-in-out;
    }

    &:before {
      content: "";
      display: block;
      width: $checkbox-box-size;
      height: $checkbox-box-size;
      border: $checkbox-box-border-size solid $checkbox-border-default;
      position: absolute;
      top: -2px;
      left: 0;
      transform: rotate(0deg) scale(1);
    }

    &:before {
      border-radius: $checkbox-checkmark-border-size;
    }

    &:hover::before {
      border-color: $checkbox-border-active;
    }

    &:after {
      content: "";
      display: block;
      width: 8px;
      height: 18px;
      border-width: 0 $checkbox-checkmark-border-size $checkbox-checkmark-border-size 0;
      border-style: solid;
      border-color: transparent $checkbox-border-active $checkbox-border-active transparent;
      position: absolute;
      top: -2px;
      left: 0;
      transform: rotate($checkbox-rotate-default) scale(0);
    }
  }

  input {
    visibility: hidden;
  }

  input[type="checkbox"],
  input[type="radio"] {
    clip-path: polygon(0 0);
  }

  &>input:checked+.label::before {
    transform: rotate($checkbox-rotate-active) scale(0);
  }

  &>input:checked+.label::after {
    left: 8px;
    transform: rotate($checkbox-rotate-active) scale(1);
    // top: -4px;
  }

  // CHECKBOX EXTRAS

  [data-onchecked] {
    padding-left: $checkbox-gutter-size;
    vertical-align: text-top;
    line-height: 15px;
  }

  [data-onchecked="show"],
  input:checked~[data-onchecked="hide"] {
    display: none;
  }

  [data-onchecked="hide"],
  input:checked~[data-onchecked="show"] {
    display: inline-block;
  }

  input:checked+.label.to-do {
    text-decoration: line-through;
  }
}