
$themes: (
  default: (
    'primary': $skyblue,
    'secondary': $onyx,
    'accent': $orange,
    'accent-2': $blue-grey,
    'accent-3': $deep-purple,
    'text': #756b61,
    'bg': $gray-100,
  )
);

@function theme-color($key, $variant) {
  $map: map-get($themes, $key);
  @return map-get($map, $variant);
}

$primary: theme-color($theme, 'primary') !default;
$secondary: theme-color($theme, 'secondary') !default;
$accent: theme-color($theme, 'accent') !default;
$accent-2: theme-color($theme, 'accent-2') !default;
$accent-3: theme-color($theme, 'accent-3') !default;
$text-color: theme-color($theme, 'text') !default;
$bg-color: theme-color($theme, 'bg') !default;

$success: $green !default;
$info: $teal !default;
$warning: $amber !default;
$danger: $red !default;

$border: $gray-300 !default;
$light-gray: $gray-200 !default;
$dark-gray: $gray-600 !default;

$theme-colors: () !default;
$theme-colors: map-merge((
  "primary": $primary,
  "secondary": $secondary,
  "accent": $accent,
  "accent-2": $accent-2,
  "accent-3": $accent-3,
  "success": $success,
  "info": $info,
  "warning": $warning,
  "danger": $danger,
  "border-gray": $border,
  "light-gray": $light-gray,
  "dark-gray": $dark-gray
), $theme-colors);

