/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 30px;

    /* Hide default HTML checkbox */
    input {
        opacity: 0;
        width: 0;
        height: 0;

        &:checked+.switch-slider {
            background-color: $primary;
        }

        &:focus+.switch-slider {
            box-shadow: 0 0 1px $primary;
        }

        &:checked+.switch-slider:before {
            -webkit-transform: translateX(30px);
            -ms-transform: translateX(30px);
            transform: translateX(30px);
        }
    }

    /* The slider */
    .switch-slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #ccc;
        -webkit-transition: .4s;
        transition: .4s;

        &:before {
            position: absolute;
            content: "";
            height: 24px;
            width: 24px;
            left: 3px;
            bottom: 3px;
            background-color: white;
            -webkit-transition: .4s;
            transition: .4s;
        }
    }

    .switch-label {
        line-height: 30px;
        position: absolute;
        left: calc(60px + 1rem);
        user-select: none;
    }

    /* Rounded sliders */
    &.round {
        .switch-slider {
            border-radius: 30px;

            &:before {
                border-radius: 50%;
            }
        }
    }

    &.small {
        width: 40px;
        height: 20px;

        input:checked+.switch-slider:before {
            -webkit-transform: translateX(20px);
            -ms-transform: translateX(20px);
            transform: translateX(20px);
        }

        .switch-label {
            line-height: 20px;
            left: calc(40px + 1rem);
        }

        .switch-slider:before {
            height: 14px;
            width: 14px;
        }
    }
}