.#{$prefix}modal {
    &:focus {
        outline: none;
    }

    display: none;
    position: fixed;
    transform-origin: center;
    top: 100px;
    left: 0;
    right: 0;
    background-color: #fafafa;
    padding: 0;
    max-height: 70%;
    width: 90%;
    margin: auto;
    overflow-y: auto;
    z-index: 8020;

    border-radius: 2px;
    will-change: top,
    opacity;

    h1,
    h2,
    h3,
    h4 {
        margin-top: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .modal-content {
        padding: 24px;
    }

    .modal-close {
        color: $dark-gray;
        position: absolute;
    top: 10px;
    right: 10px;
    background-color: transparent;
    /* outline: none; */
    padding: 0;
    border: none;
    height: 24px;
    width: 24px;
    cursor: pointer;

    &:focus {
        outline: none;
    }
    }

    .modal-footer {
        border-radius: 0 0 2px 2px;
        background-color: #fafafa;
        padding: 4px 6px;
        width: 100%;
        text-align: right;

        .button,
        .button--flat {
            margin: 6px 0;
        }
    }

    &.is-open {
        display: block;
    }

    @include sm() {
        width: 55% !important;
    }
}