.panel {
    background-color: $card-bg-color;
    border-radius: 2px;
    padding: 1rem; 

    &.raised {
        @include box-shadow(1);
    }
}

.card {
    @include box-shadow(1);
    background-color: $card-bg-color;
    border-radius: 2px;
    display: flex;
    flex-direction: column;
    margin: 1em 0;
    overflow: hidden;
    transition: box-shadow .25s;

    .card-content {
        padding: $card-padding;
        flex: 1;

        p {
            margin: 0;
        }
    }

    .card-image {
        position: relative;

        // Image background for content
        img {
            display: block;
            border-radius: 2px 2px 0 0;
            position: relative;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            width: 100%;
        }

        .card-title {
            color: $card-bg-color;
            position: absolute;
            bottom: 0;
            left: 0;
            max-width: 100%;
            padding: $card-padding;
        }
    }

    .card-title {
        line-height: 32px;
        margin-bottom: 8px;
        display: block;
        font-family: $card-title-font-family;
        font-size: 24px;
        font-weight: 300;
        user-select: none;
    }

    .card-action {

        background-color: inherit; // Use inherit to inherit color classes
        border-top: 1px solid rgba(160, 160, 160, .2);
        padding: 16px $card-padding;

        a {
            color: $card-link-color;
            margin-right: $card-padding;
            text-transform: uppercase;

            &:hover {
                color: $card-link-color-light;
            }
        }
    }
}

.profile-card {
    display: flex;
    align-items: stretch;

    .card-image {
        flex: 0 0 30%;
        min-width: 30%;

        img {
            width: 100%;
            height: 100%;
            object-position: center;
            object-fit: cover;
        }
    }

    .card-content {
        flex: 1;
        padding: 10px;
    }

    .title {
        line-height: 32px;
        margin-bottom: 8px;
        display: block;
        font-family: $card-title-font-family;
        font-size: 24px;
        font-weight: 300;
        user-select: none;
    }
}