.media {
    display: flex;
    align-items: flex-start;

    &.panel {
        border-radius: 2px;
        padding: 1rem 2rem;
        @include box-shadow(1);
    }

    &.reverse {
        .media-image {
            order: 1;
            margin-left: 1rem;
            margin-right: 0;
        }
    }

    .media-image {
        height: 80px;
        width: 80px;
        margin-right: 1rem;
        border-radius: 2px;
        background-color: #756B61;
        overflow: hidden;

        &.round {
            border-radius: 100%;
        }

        img {
            width: 100%;
            display: block;
            object-fit: cover;
            object-position: center;
            min-height: 100%;
        }
    }

    .media-content {
        flex: 1;
    }

    .media-title {
        margin: 0;
    }
}

.user {
    display: flex;
    align-items: center;

    &.panel {
        border-radius: 2px;
        padding: 1rem 2rem;
        @include box-shadow(1);
    }

    &.reverse {
        .user-image {
            order: 1;
            margin-left: 1rem;
            margin-right: 0;
        }

        .user-content {
            text-align: right;
        }
    }

    &.large {
        .user-image {
            height: 60px;
            width: 60px;
        }

        .user-name {
            font-size: 1.2rem;
        }

        .user-content {
            font-size: 1rem;
        }
    }

    .user-image {
        background: #756B61;
        height: 40px;
        width: 40px;
        border-radius: 100%;
        margin-right: 1rem;
        overflow: hidden;

        img {
            width: 100%;
            display: block;
            object-fit: cover;
            object-position: center;
            min-height: 100%;
        }
    }

    .user-name {
        font-size: 12px;
        line-height: $line-height-computed;
    }

    .user-content {
        display: flex;
        flex-direction: column;
        font-size: 10px;
    }
}

.post {
    .post-image {
        margin-bottom: 1rem;
        overflow: hidden;

        img {
            width: 100%;
            display: block;
            object-fit: cover;
            object-position: center;
            min-height: 100%;
        }
    }

    .post-title {
        display: block;
        font-size: 1.5rem;
    }

    .post-content {
        @include line-clamp(3);
    }
}